	module.exports = {"data":{"markdownRemark":{"html":"<h2>How to Divide Exponents</h2>\n<p>You can divide exponential expressions, leaving the answers as exponential expressions, as long as the bases are the same. To divide exponents (or powers) with the same base, subtract the exponents. Division is the opposite of multiplication, so it makes sense that because you add exponents when multiplying numbers with the same base, you subtract the exponents when dividing numbers with the same base.</p>\n<h4>Terminology</h4>\n<p>a^b , so a is the base and b is exponential.</p>\n<h4>Examples</h4>\n<p><img src=\"http://d2r5da613aq50s.cloudfront.net/wp-content/uploads/165077.image0.png\" alt=\"Example\"></p>\n<h4>Note</h4>\n<p>Any number to the power of zero equals 1, as long as the base number is not 0.</p>","fields":{"slug":"/mathematics/how-to-divide-exponents/"},"frontmatter":{"title":"How to Divide Exponents"}}},"pathContext":{"slug":"/mathematics/how-to-divide-exponents/"}}

